.agency-list {
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
        display: flex;
        margin-bottom: -20px;
    }
    &__item {
        background-color: #ffffff;
        width: 100%;
        padding: 40px 32px;
        box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
        border-radius: 24px;
        text-align: start;
        margin-bottom: 20px;
    
        @media screen and (min-width: 768px) {
            margin-right: 32px;
            // width: calc(100% / 2 - 30px);
        }
    
        @media screen and (min-width: 1200px) {
            margin-right: 33px;
            padding: 40px;
        }
    }
    &__item:nth-child(3n) {
        margin-right: 0;
    }
}

.agency-list__cafe {
    width: 80px;
    height: 16px;
    background: rgba(240, 209, 165, 1);
    border-radius: 4px;
    font-size: 12px;
    line-height: 1.86;
    
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.04em;
    
    color: var(--secondary-text);

    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
      width: 73px;
      height: 28px;
      font-size: 16px;

      margin-bottom: 32px;
    }
}

.cafe {
    width: 130px;
    background: rgba(194, 226, 151, 1);

    @media screen and (min-width: 768px) {
        width: 138px;
    }
}

.agency-list__title,
.timetable-list__title {
    font-size: 12px;
    line-height: 1.86;
    
    color: var(--secondary-accent-text);

    @media screen and (min-width: 768px) {
        font-size: 14px;
    }
    @media screen and (min-width: 1200px) {
        font-size: 16px;
    }
}

.agency-list__title {
    margin-bottom: 8px;

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 12px;
    }
}

.agency-list__paragraph,
.contacts-list__link {
  font-size: 12px;
  line-height: 1.5;

  color: var(--primary-text);
  @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.66;
    } 
  @media screen and (min-width: 1200px) {
      font-size: 16px;
      line-height: 1.83;
    }
}

.agency-list__paragraph {
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
        margin-bottom: 32px;
    }
}

.contacts-list {
    margin-top: 24px;
    
    @media screen and (min-width: 768px) {
        margin-top: 32px;
    }
}

.contacts-list__link {
    @media screen and (min-width: 768px) {
        line-height: 1.83;
    }
}

.timetable-list {
    border-top: 1px solid rgba(225, 225, 225, 1);
    border-bottom: 1px solid rgba(225, 225, 225, 1);
    padding-top: 24px;
    padding-bottom: 24px;

    @media screen and (min-width: 768px) {
        padding-top: 32px;
        padding-bottom: 32px;
    }

    &__time {
        font-size: 12px;
        line-height: 1.86;
    
        color: var(--accent-text);
    
        @media screen and (min-width: 768px) {
            font-size: 14px;
        }
    
        @media screen and (min-width: 1200px) {
            font-size: 16px;
        }
    }
}

.week {
    margin-bottom: 8px;

    @media screen and (min-width: 768px) {
        margin-bottom: 12px;
    }
}

.btn__loc,
.btn__fran {
    width: 161px;
    height: 44px;
    margin: 0 auto;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;

    &:nth-child(2n) {
        margin-bottom: 10px;
    }
}

.btn__loc {
    background-color: #D41342;
    color: var(--secondary-text);
}

.btn__fran {
    background-color: var(--primary-bg);
    color: #D41443;
    transition: background-color $transition-value;

    &:hover,
    &:focus {
        background-color: #ECEBEB;
        transition: background-color $transition-value;
    }
}